<template>
  <div class="uk-container">
    <div class="" uk-grid>
      <div class="uk-width-1-3@m">
        <div class="uk-margin-top">
          <div class="uk-form-label">格子个数:{{row}}</div>
          <input class="uk-range" type="range" v-model="row" min=0 max=100 step=1>
        </div>
      </div>
      <div class="uk-width-1-3">
        <div class="uk-margin-top">
        <button class="uk-button uk-button-primary" @click="resetBlock">重置</button>
        </div>
      </div>
    </div>
    <div class="out-main" uk-grid>
      <!--<div class="uk-position-center">-->
      <div class="uk-width-expand@m">
        <div class="row" v-for="item in row">
          <div class="col" v-for="item in row" @click="changeBg" @mouseover="overChangeBg" @dragstart="noDrag">
          </div>
        </div>
      </div>
      <div class="colorPlat uk-width-auto">
        <sketch-picker v-model="color"></sketch-picker>
      </div>
    </div>
  </div>

</template>

<script>
  import {Photoshop, Sketch} from 'vue-color';

  export default {
    name: "pixelcanvas",
    data() {
      return {
        canvasSize: 1000,
        row: 40,
        color: {
          hex: '#000000',
          // hsl: {
          //   h: 150,
          //   s: 0.5,
          //   l: 0.2,
          //   a: 1
          // },
          // hsv: {
          //   h: 150,
          //   s: 0.66,
          //   v: 0.30,
          //   a: 1
          // },
          // rgba: {
          //   r: 25,
          //   g: 77,
          //   b: 51,
          //   a: 1
          // },
          // a: 1
        },
        leftClick:false,
      }
    },
    components: {
      'photoshop-picker': Photoshop,
      'sketch-picker': Sketch,
    },
    watch: {
      row: function (val) {
        if (val) {
          this.row = parseInt(val);
        }
      }
    },
    mounted() {
    },
    methods: {
      changeBg() {
        let block = event.target;
        block.style.backgroundColor = this.color.hex;
      },
      overChangeBg(){
        let block = event.target;
        if(event.which == 1){
          block.style.backgroundColor = this.color.hex;
        }
      },
      resetBlock(){
        let cols = document.getElementsByClassName("col");
        for(let i = 0;i<cols.length;i++){
          cols[i].style.backgroundColor = "#9e9e9e2b";
        }
      },
      noDrag(){
        event.preventDefault();
        return false;
      },
    },
  }
</script>

<style lang="less">
  @canvasSize: 1000px;
  @borderColor: rgba(255, 255, 255, 0.62);
  @latColor: #9e9e9e2b;
  @latSize: 15px;
  @scaleFac:1.5;

  .out-main {
  }

  .canvas-container {
    zoom: 0.5;
  }

  .pixel-canvas {
    width: @canvasSize;
    height: @canvasSize;
  }

  .row, .col {
    margin: 0;
    padding: 0;
  }

  .row {
    display: block;
    height: @latSize;
    border-top: 1px solid @borderColor;
    border-right: 1px solid @borderColor;
    position:relative;
  }

  .row:last-of-type {
    border-bottom: 1px solid @borderColor;
  }

  .col {
    float: left;
    border-left: 1px solid @borderColor;
    background-color: @latColor;
    width: @latSize;
    height: @latSize;
    cursor: pointer;
  }
  .col:hover{
    transform:scale(@scaleFac,@scaleFac);
    /*background-color: #fff;*/
    border:none;
    position:relative;
    z-index:100;
    transition:all 0.3s;
  }

</style>



