<template>
  <div class="uk-container uk-margin-top">
    <div class="" uk-alert>
            通过模板生成代码,占位符是
      <input type="text" class="tempMark" v-model="holder">
    </div>
    <div class="uk-grid" uk-grid>
      <div class="uk-width-1-2">
        <div>
          <div class="uk-form-label">template:</div>
          <textarea name="template" id="template" cols="30" rows="10" class="tempText uk-textarea"
                    v-model="template" @keydown.tab="tabMarkdown"></textarea>
        </div>
        <div class="uk-margin-top">
          <button class="uk-button uk-button-primary" @click="generateTemplate">生成</button>
          <button class="uk-button uk-button-danger" @click="clear" :class="{'uk-hidden':!status}">重置</button>
          <div class="uk-align-right">
            <button class="uk-button uk-button-secondary" @click="getCode(1)" :class="{'uk-hidden':!status}">增加</button>
            <button class="uk-button uk-button-secondary" @click="getCode(0)" :class="{'uk-hidden':!status}">覆盖</button>
          </div>
        </div>
      </div>
      <div class="uk-width-1-2">
        <div class="" v-for="(item,index) in markList">
          <div class="uk-form-label">{{index}}:</div>
          <input type="text" class="uk-input" v-model="markList[index]" @focus="focus">
        </div>
      </div>
    </div>
    <div class="uk-grid uk-margin-top" uk-grid :class="{'uk-hidden':!status}">
      <div class="uk-width-1-1@l">
        <div class="uk-form-label">生成代码:</div>
        <textarea name="result" id="result" cols="30" rows="10" v-model="generateCode" class="uk-textarea"></textarea>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "templateCode",
    data() {
      return {
        holder:'%%',
        template: "",   //模板
        markList: [],   //占位符列表
        generateCode: "", //生成代码
        status: false,    //当前是否已生成模板
      }
    },
    mounted(){
      var holder = window.localStorage.holder;
      if(holder==undefined || holder=='' || holder=='undefined'){
        this.holder = "%%";
      }else{
        this.holder = holder;
      }
    },
    watch:{
      holder:function(val){
        window.localStorage.holder = val;
      }
    },
    methods: {
      generateTemplate() {
        this.status = true;
        this.generateCode = "";
        let len = (this.template.match(eval("/"+this.holder+"/g")) || []).length;
        this.markList = [];
        for (let i = 0; i < len; i++) {
          this.markList.push("");
        }
      },
      clear() {
        this.template = "";
        this.markList = [];
        this.generateCode = "";
        this.status = false;
      },
      getCode(mark = 1) {
        this.temp = this.template + "\n";
        for (let index in this.markList) {
          this.temp = this.temp.replace(this.holder, this.markList[index]);
        }
        if (mark == 0) {
          this.generateCode = this.temp;
        } else {
          this.generateCode += this.temp;
        }
      },
      tabMarkdown(e) { // 禁止tabs键的默认事件,并设置tab等于4个空格
        e.preventDefault();
        this.textarea = document.getElementById("template");
        let indent = '    ';
        let start = this.textarea.selectionStart;
        let end = this.textarea.selectionEnd;
        let selected = window.getSelection().toString();
        selected = indent + selected.replace(/\n/g, '\n' + indent);
        this.textarea.value = this.textarea.value.substring(0, start) + selected
          + this.textarea.value.substring(end);
        this.textarea.setSelectionRange(start + indent.length, start
          + selected.length);
      },
      focus(event) {
        event.currentTarget.select();
      },
    },
  }
</script>

<style lang="less">
  @import '../common/style/templateCode';
  .tempMark{
    width:40px;
    height:20px;
    border:none;
  }
</style>



