<template>
  <section>
    <menus @switch="switchComponent"></menus>
    <keep-alive>
      <component v-bind:is="showComponent"></component>
    </keep-alive>
  </section>
</template>

<script>
  import menus from "@/components/menus"
  import templateCode from '@/components/templateCode'
  import index from '@/components/index'
  import pixelcanvas from '@/components/pixelcanvas'

  export default {
    name: "layouts",
    data(){
      return{
        showComponent:index,
        componentMap:{0:index,1:templateCode,2:pixelcanvas},
      }
    },
    components: {
      menus,
      templateCode,
      index,
      pixelcanvas,
    },
    methods:{
      switchComponent(index){
        this.showComponent = this.componentMap[index];
      },
    },
  }
</script>

<style scoped>

</style>



