<template>
  <div>
    <chart :id="'goldChart'" :option="goldPrices" class="chart" v-if="isView"></chart>
  </div>
</template>

<script>
  import Chart from '@/components/Chart'

  export default {
    name: "gold-price",
    components:{
      Chart,
    },
    data() {
      return {
        goldPrices: {
          // Make gradient line here
          visualMap: {
            show: false,
            type: 'continuous',
            seriesIndex: 0,
            min: 0,
            max: 400
          },
          title: {
            left: 'center',
            text: 'Gold Price'
          },
          tooltip: {
            trigger: 'axis'
          },
          xAxis: {
            data: [],
            boundaryGap: false,
          },
          yAxis: {
            splitLine: {show: false},
            type:"value",
            scale:true,
          },
          series:[],
        },
        priceList:{},
        isView:false,
      }
    },
    mounted:function(){
      this.getGoldPrices();
    },
    methods:{
      getGoldPrices(){
        const url = "http://localhost:8080/api/task/golds";
        this.$http.get(url).then((result)=>{
          let res = result.data;
          if(res.rspCode=="000000"){
            this.priceList = res.data;
            this.initGoldPrices(res.data);
            this.isView = true;
          }
        }).catch((error)=>{
          console.log("failed");
          this.isView = false;
        });
      },
      initGoldPrices(data){
        console.log(data);
        data.sort(function(a,b){
          return a.createTime-b.createTime;
        });
        let series_item = {
          name: "price",
          type: 'line',
          showSymbol: false,
          markPoint:{
            data:[
              {type: 'max', name: '最大值'},
              {type: 'min', name: '最小值'}
            ]
          },
          data: [],
        };
        for(let item in data){
          this.goldPrices.xAxis.data.push(data[item].time);
          let temp_price = data[item].price;
          temp_price = parseFloat(temp_price).toFixed(2);
          series_item.data.push(temp_price);
        }
        this.goldPrices.series.push(series_item);
      },
    }
  }
</script>

<style scoped>

</style>



